import { Button, Cell, Field, Input } from "@taroify/core"
import { MusicOutlined, SmileOutlined, WarningOutlined } from "@taroify/icons"
import * as React from "react"
import { useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicField() {
  const [value, setValue] = useState("")
  return (
    <Cell.Group inset>
      <Field label="">
        <Input placeholder="" value={value} onChange={(e) => setValue(e.detail.value)} />
      </Field>
    </Cell.Group>
  )
}

function CustomField() {
  const [text, setText] = useState("")
  const [idcard, setIdcard] = useState("")
  const [number, setNumber] = useState("")
  const [digit, setDigit] = useState("")
  const [password, setPassword] = useState("")
  return (
    <Cell.Group inset>
      <Field label="">
        <Input placeholder="" value={text} onChange={(e) => setText(e.detail.value)} />
      </Field>
      <Field label="">
        <Input
          type="idcard"
          placeholder=""
          value={idcard}
          onChange={(e) => setIdcard(e.detail.value)}
        />
      </Field>
      <Field label="">
        <Input
          type="number"
          placeholder=""
          value={number}
          onChange={(e) => setNumber(e.detail.value)}
        />
      </Field>
      <Field label="">
        <Input
          type="digit"
          placeholder=""
          value={digit}
          onChange={(e) => setDigit(e.detail.value)}
        />
      </Field>
      <Field label="">
        <Input
          password
          placeholder=""
          value={password}
          onChange={(e) => setPassword(e.detail.value)}
        />
      </Field>
    </Cell.Group>
  )
}

function DisabledField() {
  return (
    <Cell.Group inset>
      <Field label="">
        <Input placeholder="" readonly />
      </Field>
      <Field label="">
        <Input placeholder="" disabled />
      </Field>
    </Cell.Group>
  )
}

function IconField() {
  return (
    <Cell.Group inset>
      <Field label="" icon={<SmileOutlined />} rightIcon={<WarningOutlined />}>
        <Input placeholder="" />
      </Field>
      <Field label="" icon={<MusicOutlined />}>
        <Input placeholder="" clearable />
      </Field>
    </Cell.Group>
  )
}

function ErrorField() {
  return (
    <Cell.Group inset>
      <Field label="" required>
        <Input placeholder="" color="danger" />
      </Field>
      <Field required label="" feedbackStatus="invalid" feedback="">
        <Input placeholder="" />
      </Field>
    </Cell.Group>
  )
}

function ButtonField() {
  return (
    <Cell.Group inset>
      <Field align="center" label="">
        <Input placeholder="" />
        <Button size="small" color="primary">
          
        </Button>
      </Field>
    </Cell.Group>
  )
}

export default function FieldDemo() {
  return (
    <Page title="Field " className="field-demo">
      <Block title="">
        <BasicField />
      </Block>
      <Block title="">
        <CustomField />
      </Block>
      <Block title="">
        <DisabledField />
      </Block>
      <Block title="">
        <IconField />
      </Block>
      <Block title="">
        <ErrorField />
      </Block>
      <Block title="">
        <ButtonField />
      </Block>
    </Page>
  )
}
